<template>
  <v-container grid-list-xs>
    <v-dialog
      v-model="enrollingProgress"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-progress-linear height="17" indeterminate color="primary">Enrolling Applicants</v-progress-linear>
    </v-dialog>
    <v-card>
      <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
        Schedule Exam
      </v-toolbar>
      <v-card-text>
        <v-row>
          <v-col cols="4">
            <v-autocomplete
              label="Cadre Category*" 
              v-model="schedule.cadre" 
              :items="cadreCategoryCodes"
              outlined
              multiple
              chips
              deletable-chips
              hide-details="auto"
              item-text="display"
              item-value="code"
              :rules="cadrerules"
              return-object
              dense
              @change="errors.cadre = []"
            />
          </v-col>
          <v-col cols="4">
            <v-autocomplete
              label="Assessment Types*" 
              v-model="schedule.assements" 
              :items="assessmentCodes"
              outlined
              multiple
              chips
              deletable-chips
              hide-details="auto"
              item-text="display"
              item-value="code"
              :rules="assessmentrules"
              return-object
              dense
              @change="errors.assement = []"
            />
          </v-col>
          <v-col cols="4">
            <v-text-field
              @change="errors.passmark = []"
              label="Pass Mark*"
              v-model="schedule.passmark"
              outlined
              hide-details="auto"
              :rules="passmarkrules"
              dense
            />
          </v-col>
          <v-col cols="4">
            <v-switch label="Auto Enroll Applicants?" v-model="schedule.autoEnroll"></v-switch>
          </v-col>
          <v-col cols="4">
            <v-menu
              v-model="startDateMenu"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="schedule.startDateFormatted"
                  label="Start Date*"
                  prepend-icon="mdi-event"
                  readonly
                  v-on="on"
                  :rules="startdaterules"
                  outlined
                  dense
                />
              </template>
              <v-date-picker
                v-model="schedule.startDate"
                @change="schedule.startDateFormatted = formatDate(schedule.startDate)"
                @input="startDateMenu = false"
              />
            </v-menu>
          </v-col>
          <v-col cols="4">
            <v-menu
              v-model="endDateMenu"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="schedule.endDateFormatted"
                  label="End Date*"
                  prepend-icon="mdi-event"
                  readonly
                  v-on="on"
                  :rules="enddaterules"
                  outlined
                  dense
                />
              </template>
              <v-date-picker
                v-model="schedule.endDate"
                @change="schedule.endDateFormatted = formatDate(schedule.endDate)"
                @input="endDateMenu = false"
              />
            </v-menu>
          </v-col>
          <v-col cols="12">
            <v-textarea
              label="Description"
              v-model="schedule.description"
              outlined
              hide-details="auto"
              dense
            />
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn @click="addSchedule" :disabled="disabled" color="success">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>
<script>
export default {
  data() {
    return {
      enrollingProgress: false,
      cadreCategoryCodes: [],
      assessmentCodes: [],
      schedule: {
        cadre: [],
        assements: [],
        passmark: '',
        startDate: '',
        startDateFormatted: '',
        endDate: '',
        endDateFormatted: '',
        description: ''
      },
      startDateMenu: false,
      endDateMenu: false,
      errors: {
        passmark: [],
        cadre: [],
        assement: [],
        startdate: [],
        enddate: []
      }
    }
  },
  computed: {
    passmarkrules: function() {
      const num_check = v => {
        this.errors.passmark = []
        let val = (!isNaN(v) && /^-?\d+(\.\d+)?$/.test(v)) || "Pass Mark must be an Decimal"
        if(val != true) {
          this.errors.passmark = [val]
        }
        return val
      }
      let rules = [ num_check ]
      return rules
    },
    cadrerules() {
      return [
        (v) => {
          if(v.length === 0) {
            this.errors.cadre = ['Cadre Category is required']
            return "Cadre Category is required"
          }
          return true
        }
      ]
    },
    assessmentrules() {
      return [
        (v) => {
          if(v.length === 0) {
            this.errors.assement = ["Assessment is required"]
            return "Assessment is required"
          }
          return true
        }
      ]
    },
    startdaterules() {
      return [
        (v) => {
          this.errors.startdate = []
          if(!v) {
            this.errors.startdate = ["Start date is required"]
            return "Start date is required"
          }
          if(this.schedule.endDate) {
            v = this.$moment(v, "DD-MM-YYYY").format("YYYY-MM-DD")
            if(!this.$moment(v).isSameOrBefore(this.schedule.endDate)) {
              this.errors.startdate = ["Start date must be before start date"]
              return "Start date must be before start date"
            }
          }
          return true
        }
      ]
    },
    enddaterules() {
      return [
        (v) => {
          this.errors.enddate = []
          if(!v) {
            this.errors.enddate = ["End date is required"]
            return "Start date is required"
          }
          if(this.schedule.startDate) {
            v = this.$moment(v, "DD-MM-YYYY").format("YYYY-MM-DD")
            if(!this.$moment(v).isSameOrAfter(this.schedule.startDate)) {
              this.errors.enddate = ["End date must be after start date"]
              return "End date must be after start date"
            }
          }
          return true
        }
      ]
    },
    disabled() {
      if(this.errors.cadre.length === 0 && this.errors.assement.length === 0 && this.errors.passmark.length === 0 && this.errors.startdate.length === 0 && this.errors.enddate.length === 0) {
        return false
      }
      return true
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}-${month}-${year}`;
    },
    async loadCadreCategoriesCodes() {
      this.cadreCategoryCodes = await this.$fhirutils.expand("http://ihris.org/fhir/StructureDefinition/nmcb-cadre-category-valueset")
    },
    async loadAssessmentsCodes() {
      this.assessmentCodes = await this.$fhirutils.expand("http://ihris.org/fhir/StructureDefinition/nmcb-exam-assessment-type-valueset")
    },
    addSchedule() {
      let schedule = {
        resourceType: "Basic",
        meta: {
          profile: ["http://ihris.org/fhir/StructureDefinition/nmcb-exam-scheduling-profile"]
        },
        extension: [{
          url: "http://ihris.org/fhir/StructureDefinition/nmcb-exam-passmark",
          valueDecimal: this.schedule.passmark
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/start-date",
          valueDate: this.schedule.startDate
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/exam-auto-enroll",
          valueBoolean: this.schedule.autoEnroll
        }]
      }
      for(let cadre of this.schedule.cadre) {
        schedule.extension.push({
          url: "http://ihris.org/fhir/StructureDefinition/nmcb-cadre-category",
          valueCoding: {
            system: cadre.system,
            code: cadre.code,
            display: cadre.display
          }
        })
      }
      for(let assement of this.schedule.assements) {
        schedule.extension.push({
          url: "http://ihris.org/fhir/StructureDefinition/nmcb-exam-assessment-type",
          valueCoding: {
            system: assement.system,
            code: assement.code,
            display: assement.display
          }
        })
      }
      if(this.schedule.endDate) {
        schedule.extension.push({
          url: "http://ihris.org/fhir/StructureDefinition/end-date",
          valueDate: this.schedule.endDate
        })
      }
      if(this.schedule.description) {
        schedule.extension.push({
          url: "http://ihris.org/fhir/StructureDefinition/comments",
          valueString: this.schedule.description
        })
      }
      schedule.extension.push({
        url: "http://ihris.org/fhir/StructureDefinition/nmcb-examresults-status",
        valueCoding: {
          system: "http://ihris.org/fhir/CodeSystem/nmcb-examresults-status-codesystem",
          code: 'open',
          display: 'Ongoing Results Upload'
        }
      })
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify(schedule)
      }
      fetch("/fhir/Basic", opts).then((response) => {
        this.overlay = false;
        if(response.status === 201) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Schedule added successfully",
          });
          response.json().then((exam) => {
            if(this.schedule.autoEnroll) {
              this.enrollingProgress = true
              let opts = {
                method: "POST",
                headers: {
                  "Content-Type": "application/json"
                },
                redirect: 'manual',
                body: JSON.stringify({
                  cadres: this.schedule.cadre,
                  exam: exam.id
                })
              }
              fetch("/site/nmcb/autoEnroll", opts).then((response) => {
                this.enrollingProgress = false
                if(response.status === 200) {
                    this.$store.commit("setMessage", {
                      type: "success",
                      text: "All applicants were enrolled successfully",
                    });
                } else {
                  this.$store.commit("setMessage", {
                    type: "error",
                    text: "Not all applicants were automatically enrolled, enroll the rest manually."
                  });
                }
                this.$router.push({
                  path: "/custom/nmcb-scheduled-exam?path=/components/exams&exam=" + exam.id
                })
              }).catch(() => {
                this.enrollingProgress = false
                this.$store.commit("setMessage", {
                  type: "error",
                  text: "Not all applicants were automatically enrolled, enroll the rest manually."
                });
                this.$router.push({
                  path: "/custom/nmcb-scheduled-exam?path=/components/exams&exam=" + exam.id
                })
              })
            } else {
              this.$router.push({
                path: "/custom/nmcb-scheduled-exam?path=/components/exams&exam=" + exam.id
              })
            }
          })
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to add schedule."
          });
        }
      }).catch(() => {
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to add schedule."
        });
      })
    }
  },
  created() {
    this.loadCadreCategoriesCodes()
    this.loadAssessmentsCodes()
  }
}
</script>